import React from 'react';
import styled from '@emotion/styled';
import { StaticQuery, graphql } from 'gatsby';
import Link from './link';
import Loadable from 'react-loadable';
import config from '../../config.js';
import LoadingProvider from './mdxComponents/loading';
import Sidebar from './sidebar';
import headerImage from '/src/components/images/logo.png'

const isSearchEnabled = config.header.search && config.header.search.enabled ? true : false;

let searchIndices = [];

if (isSearchEnabled && config.header.search.indexName) {
  searchIndices.push({
    name: `${config.header.search.indexName}`,
    title: `Results`,
    hitComp: `PageHit`,
  });
}

const LoadableComponent = Loadable({
  loader: () => import('./search/index'),
  loading: LoadingProvider,
});

function myFunction() {
  var x = document.getElementById('navbar');

  if (x.className === 'topnav') {
    x.className += ' showMenu';
    console.log('TEST')
  } else {
    x.className = 'topnav';
  }
}

function show() {
  var x = document.getElementById('navbar');
  x.classList.add("showMenu");
  console.log('SHOW()')

}

const Header = ({ location, isDarkThemeActive, toggleActiveTheme }) => (
  <StaticQuery
    query={graphql`
      query headerTitleQuery {
        site {
          siteMetadata {
            headerTitle
            logo {
              link
              image
            }
            headerLinks {
              link
              text
            }
          }
        }
      }
    `}
    render={data => {
      const {
        site: {
          siteMetadata: { headerTitle, githubUrl, headerLinks, helpUrl, logo },
        },
      } = data;

      // const mainLogoLink = logo.link !== '' ? logo.link : 'https://ryanjeon.com/';

      return (
        <div className={'navBarWrapper'}>
          <div className={'navBarLeft'}>
            <div
              onClick={show}
              className={'navBarToggle'}
              onKeyDown={show}
              role="button"
              tabIndex={0}
            >
              <span className={'iconBar'}></span>
              <span className={'iconBar'}></span>
              <span className={'iconBar'}></span>
            </div>
            <ul id="navbar">
              {headerLinks.map((link, key) => {
                if (link.link !== '' && link.text !== '') {
                  return (
                    <li key={key}>
                      <a
                        className="sidebarLink"
                        href={link.link}
                        target="_blank"
                        rel="noopener noreferrer"
                        dangerouslySetInnerHTML={{ __html: link.text }}
                      />
                    </li>
                  );
                }
              })}
            </ul>
          </div>
          <nav className={'navBarBrand'}>
            <Link to="/"><img src={headerImage} className={'logo'}/></Link>
          </nav>
          <div className={'navBarRight'}></div>
        </div>
      );
    }}
  />
);

export default Header;
